<script>
import { onMount } from 'svelte';

import Button from './Button.svelte';
import Controls from './Controls.svelte';
import Details from './Details.svelte';
import DeveloperForm from './DeveloperForm.svelte';
import List from './List.svelte';
import Notes from './Notes.svelte';
import Search from './Search.svelte';
import Tabs from './Tabs.svelte';
import Timeline from './Timeline.svelte';

let developers = [];
let labels = [];
let selectedDeveloper = null;
let selectedPanel = 0;

onMount( async () => {
  developers = await fetch( '/api/developer' )
  .then( ( response ) => response.json() );

  labels = await fetch( '/api/label' )
  .then( ( response ) => response.json() );  
} );

function doAdd( evt ) {
  console.log( 'Add developer.' );
}

function doCancel( evt ) {
  console.log( 'Total bummer!' );
}

function doDeveloperChange( evt ) {
  selectedDeveloper = evt.detail.selectedItem;
}

function doSave( evt ) {
  console.log( 'Hooray!' );
}
</script>

<style>
article {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

aside {
  background-color: #f3f3f3;
  display: flex;
  flex-direction: column;
  min-width: 300px;
}

div.panel {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
}

div.search {
  display: flex;
  flex-direction: row;
}

h4 {
  background-color: #dcdcdc;      
  color: #393939;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 48px;
  margin: 0;
  padding: 0 16px 0 16px;  
}

p.developer {
  border-bottom: solid 1px #dcdcdc;
  border-top: solid 1px transparent;  
  color: #393939;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  height: 46px;
  line-height: 46px;
  margin: 0;
  padding: 0 16px 0 16px;
}

p.label {
  background-image: url( /img/label.svg );
  background-position: center left 15px;
  background-repeat: no-repeat;
  background-size: 18px;  
  border-bottom: solid 1px #dcdcdc;
  border-top: solid 1px transparent;  
  color: #393939;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 14px;
  font-weight: 400;
  height: 46px;
  line-height: 46px;
  margin: 0;
  padding: 0 16px 0 40px;
}
</style>

<div class="panel">

  <!-- Left panel -->
  <aside>

    <!-- Search -->
    <div class="search">
      <Search/>
      <Button label="Add" icon="/img/add.svg" on:click="{doAdd}"/>
    </div>

    <!-- Developer list -->
    <h4>Developers</h4>
    <List data="{developers}" let:item="{developer}">
      <p class="developer">{developer.name}</p>
    </List>

    <!-- Label list -->
    <!-- Collapsable -->
    <Details summary="Labels">
      <List data="{labels}" let:item="{label}">
        <p class="label">{label.name}</p>
      </List>      
    </Details>

  </aside>

  <!-- Center panel -->
  <article>

    <!-- Views/tabs -->
    <Tabs on:tab="{( evt ) => selectedPanel = evt.detail}"/>

    <!-- Overview form -->
    <DeveloperForm visible="{selectedPanel === 0 ? true : false}"/>

    <!-- Social media timeline -->
    <Timeline visible="{selectedPanel === 1 ? true : false}"/>

    <!-- Notes editor -->
    <Notes 
      developer="{selectedDeveloper}" 
      visible="{selectedPanel === 2 ? true : false}"/>

    <!-- Control bar -->
    <!-- Save, cancel, etc-->
    <Controls 
      on:cancel="{doCancel}"
      on:save="{doSave}" 
      visible="{selectedPanel < 2 ? true : false}"/>
  </article>

  <!-- Right panel -->
  <!-- Statistics -->
  <aside></aside>

</div>
